/* >1040px, <1040px, <768px */
/* >65em, <65em, <48em */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

* {
  padding: 0;
  margin: 0;
  list-style: none;
  box-sizing: border-box;
}

/* nav */
.navbar {
  font-family: 'Poppins';
  justify-content: space-between;
  display: flex;
  margin: 50px auto 35px;
  padding: 0% 10%;
}

.controls { height: 60px; }

.logo {
  display: flex;
  font-size: 30px;
  font-weight: 700;
  align-items: center;
}
.nav-icon, #state {
  display: none;
}
.nav-links {
  transition: 0.3s;
}
.nav-links a:not(.primary) {
  font-size: 16px;
  line-height: 44px;
  font-weight: 400;
  color: #242f51;
  text-decoration: none;
  padding: 5px 10px;
  transition: .3s;
}

.nav-links a:hover:not(.primary) {
  background-color: #e2eaed;
}

.nav-links a.primary {
  margin: 0px 0px 0px 25px;
}


.app { color: #fcae61; }
.lab { color: #242f51; }


/* header */
.header {
  background: url('../img/layer_3_448.png') 0% 20% no-repeat,
              url('../img/vector_smart_object_651.png') no-repeat,
              url('../img/layer_5_646.png') bottom repeat-x;
  display: flex;
  min-height: 835px;
}

.phone {
  background:  url('../img/phone.png') 35% 0% no-repeat;
  min-width: 446px;
}

/* header block */
.main-block {
  font-family: 'Poppins';
  max-width: 520px;
  margin: 15% 8% 0% 15%;
}

.cap {
  display: flex;
  align-items: center;
  background-color: #e2eaed;
  border-radius: 17.5px;
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  color: #242f51;
  max-width: 362px;
}

.cap .icon {
  background: url('../img/icon_star.png') center no-repeat;
  width: 39px;
  height: 35px;
  background-color: #feaa25;
  border-radius: 17.5px;
  margin-right: 10px;
}

.main-title {
  font-size: 60px;
  line-height: 75px;
  font-weight: 600;
  color: #242f51;
}

.sub-title {
  font-size: 18px;
  line-height: 28px;
  font-weight: 400;
  color: #616368;
  margin: 15px 0px 30px;
}

.main-links {
  display: flex;
  align-items: center;
}

.main-links a:not(.primary) {
  font-size: 16px;
  line-height: 44px;
  font-weight: 400;
  color: #2500f9;
  text-decoration: none;
}

.watch {
  min-width: 153px;
  display: block;
}

.primary {
  font-size: 16px;
  line-height: 57px;
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  padding: 0px 40px;
  background-color: #2500f9;
  border-radius: 28.5px;
  margin-right: 20px;
  width: 170px;
}

/* content */
.main-section, .faq {
  font-family: 'Poppins';
  margin: 0px auto 0px;
  width: 75%;
}

.faq {
  margin-bottom: 140px;
}

.section-block {
  display: block;
  margin: 130px auto 85px;
}

.section-title {
  font-size: 36px;
  line-height: 46px;
  font-weight: 600;
  color: #242f51;
  margin: 0 auto 35px;
  text-align: center;
}

.section-sub-title {
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #616368;
  margin: 0 auto;
  text-align: center;
}

/* offers */
.offers-block {
  font-family: 'Poppins';
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.offer {
  margin: 0px 15px 70px;
  width: 275px;
}

.offer-img {
  width: 88px;
  height: 88px;
  border-radius: 50%;
  margin: 0 auto;
}

.offer-title {
  text-align: center;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
  color: #242f51;
  margin: 35px auto;
}

.offer-text {
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  color: #616368;
  text-align: center;
}

/* icons */
.ffecef { background: #ffecef url('../img/web_design_437.png') center no-repeat; }
.eaf9ff { background: #eaf9ff url('../img/trophy_438.png') center no-repeat; }
.fff6d3 { background: #fff6d3 url('../img/computer_439.png') center no-repeat; }
.e4ffee { background: #e4ffee url('../img/customer_support_440.png') center no-repeat; }
.f4f4ff { background: #f4f4ff url('../img/ui_441.png') center no-repeat; }
.fdeee4 { background: #fdeee4 url('../img/customer_service_442.png') center no-repeat; }

/* faq */
.tab {
  width: 100%;
  color: white;
  overflow: hidden;
  margin-bottom: 10px;
}

.tab > input { display: none; }

.tab-label {
  display: flex;
  justify-content: space-between;
  padding: 20px 50px;
  background: #f5f7fa;
  color: #242f51;
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
}

.tab-label::after {
  content: '';
  background: url('../img/plus_406.png') center no-repeat;
  min-width: 20px;
  min-height: 30px;
  text-align: center;
  transition: all 0.35s;
}

.tab-content {
  max-height: 0;
  padding: 0px 50px;
  color: #2c3e50;
  background: #f5f7fa;
  transition: all 0.35s;
}

.tab-close {
  display: flex;
  justify-content: flex-end;
  padding: 0px 50px;
  font-size: 0.75em;
  background: #2c3e50;
  cursor: pointer;
}

input:checked + .tab-label::after {
  display: none;
}

input:checked ~ .tab-content {
  max-height: 100vh;
  padding: 35px 50px 80px;
}


@media (max-width: 1040px) {
  /* nav */
  .navbar, .nav-icon { display: block; }
  .nav-icon { cursor: pointer; padding: 5px 10px 0px; }
  .nav-icon:hover {
    background-color: #e2eaed;
  }
  .controls {
    display: flex;
    align-items: center;
    justify-content: inherit;
  }

  #state:checked ~ .nav-links { display: block; }
  #state:not(:checked) ~ .nav-links { display: none; }

  .nav-links {
    text-align: center;
  }
  .nav-links a.primary {
    margin: 10px auto;
  }
  .nav-links a {
    display: block;
  }


  .header {
    background-size: contain;
    display: block;
  }

  .main-block {
    background-color: #fff;
    margin: 0px auto;
  }

  .phone {
    background-position: center;
    height: 835px;
  }
}